<template>
    <div>
        <navbar/>
        <div>
            <button class="button2" type="button" @click="solve"> {{ solved? 'hide solution': 'show solution' }} </button>
        </div>
        <div class="grid-container">
            <div v-for="item in this.puzzle" 
                :key="item.index"
                :class='styles[item.index]'
                >
                <input :class="item.readonly?'cell-ro':'cell'" v-model="item.value" type="text" maxlength="1" :readonly="item.readonly?true:false" @input="checkInput(item.index)">
            </div>
        </div>
    </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue'
import {makepuzzle, solvepuzzle} from 'sudoku'

export default {
    name: 'Sudoku',
    components:{
        navbar: Navbar
    },
    data(){
        let result = this.getPuzzle();
        let original = Array();
        result.puzzle.forEach((i)=>{
            original.push(i.value);
        })
        return{
            p: original,
            puzzle: result.puzzle,
            solution: result.solution,
            solved: false,
        }
    },
    methods:{
        // => notation will cause problems when listener calls this function
        // Only checks the newly updated input
        checkInput(index){
            this.puzzle.forEach((instance)=>{
                instance.conflict = false;
            })
            let input = this.puzzle[index];
            if(input.value == '') return;
            let col = this.getCol(index);
            let row = this.getRow(index);
            // check this row
            for(let i = 0; i < 9; i++){
                if(i == col-1) continue;
                else{
                    let num = this.puzzle[(row-1)*9+i];
                    if(num.value == input.value && num.value != null && num.value != ''){
                        num.conflict = true;
                        input.conflict = true;
                        console.log(num);
                    }
                }
            }
            // check each column
            for(let j = 0; j < 9; j++){
                if(j == row-1) continue;
                else{
                    let num = this.puzzle[j*9+col-1];
                    if(num.value == input.value && num.value != null && num.value != ''){
                        num.conflict = true;
                        input.conflict = true;
                        console.log(num);
                    }
                }
            }
            let startRow = Math.floor((row-1)/3)*3;
            let startCol = Math.floor((col-1)/3)*3;
            // check each block
            for(let i = startCol; i < startCol+3; i++){
                for(let j = startRow; j < startRow+3; j++){
                    if(j == row-1 && i==col-1) continue;
                    else{
                        let num = this.puzzle[j*9+i];
                        if(num.value == input.value && num.value != null && num.value != ''){
                            num.conflict = true;
                            input.conflict = true;
                            console.log(num);
                        }
                    }
                }
            }
        },
        getPuzzle: ()=>{
            let p = makepuzzle();
            let solution = solvepuzzle(p);
            console.log('solution:' + solution +', puzzle:' + p)
            let i = 0;
            let s = []
            p.forEach((each)=>{
                let index = i;
                let row = Math.floor(index/9) + 1;
                let className = '';
                let col = (index-Math.floor(index/9)*9)%3;
                switch(row){
                    case 1:
                    case 4:
                    case 7:
                        switch(col){
                            case 0:
                                className = 'grid-item-ul';
                                break;
                            case 1:
                                className = 'grid-item-u';
                                break;
                            case 2:
                                className = 'grid-item-ur';
                                break;
                            default: 
                                alert('error');
                        }
                        break;
                    case 2:
                    case 5:
                    case 8:
                        switch(col){
                            case 0:
                                className = 'grid-item-l';
                                break;
                            case 1:
                                className = 'grid-item-c';
                                break;
                            case 2:
                                className = 'grid-item-r';
                                break;
                            default: 
                                alert('error');
                        }
                        break;
                    case 3:
                    case 6:
                    case 9:
                        switch(col){
                            case 0:
                                className = 'grid-item-bl';
                                break;
                            case 1:
                                className = 'grid-item-b';
                                break;
                            case 2:
                                className = 'grid-item-br';
                                break;
                            default: 
                                alert('error');
                        }
                        break;
                    default: 
                        alert('error');

                }
                let v = {
                    value: (each==null)?null:each+1,
                    index: i++,
                    style: (each==null)?className:className+'-ro',
                    readonly: (each==null)?false:true,
                    conflict: false
                }
                s.push(v)
            })
            return{
                puzzle: s,
                solution: solution
            }
        },
        getRow(index){
            return Math.floor(index/9)+1;
        },
        getCol(index){
            return index%9+1;
        },
        getIndex(row, col){
            return (row-1)*9+col-1;
        },
        solve(){
            if(!this.solved){
                this.puzzle.forEach((i)=>{
                    i.value = this.solution[i.index]+1;
                })
            }
            if(this.solved){
                this.puzzle.forEach((i)=>{
                    i.value = this.p[i.index];
                })
                console.log(this.p)
            }
            this.solved = !this.solved;
        }
    },
    computed:{
        styles(){
            let retVal = Array();
            this.puzzle.forEach((p)=>{
                retVal.push(p.style+(p.conflict?'-conflict':''));
            })
            return retVal;
        }
    }
}
</script>

